'use-client'

import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import * as React from 'react';

const ChatScreen: React.FC = () => {

    const [message, setMessage] = React.useState<string>('')
    
    return (
        <div className='p-4'>
            <div className='fixed bottom-4 w-100 flex gap-3'>
                <Input value={message} onChange={e => setMessage(e.target.value)} placeholder='Enter your query here' />
                <Button disabled={!message.trim()} className='bg-white text-black'>Send</Button>
            </div>
        </div>

    );

}

export default ChatScreen;